import { FormGroup } from '@angular/forms';


export default class ValidationHelper {
    private static validationMessages = {
        required: 'Trng ny l bt buc nhp',
        email: 'Email khng ng nh dng',
        website: 'Website khng ng nh dng',
        probability: 'Gi tr phi nm trong khong t 0-100',
        phoneNumber: 'S in thoi phi c t 10 n 20 ch s',
        faxNumber: 'Fax phi c t 10 n 20 ch s',
        password: 'Mt khu phi c t nht 6 k t',
        taxNumber: 'M s thu phi c t 10 n 13 ch s',
        maxlength: 'Trung ny phi nh hn '
    };

    static getInvalidMessages(
        form: FormGroup,
        formErrors: object): string[] {
        if (!form) { return; }
        const errorMessages = [];
        for (const field in formErrors) {
            formErrors[field] = '';
            const control = form.get(field);
            if (control && !control.valid) {
                // const messages = validationMessages[field];
                // tslint:disable-next-line:forin
                for (const key in control.errors) {
                    if (key === 'maxlength') {
                        formErrors[field] += this.validationMessages[key] + control.errors[key].requiredLength + ' k t';
                    } else {
                        formErrors[field] += this.validationMessages[key] + '';
                    }
                }
            }
        }

        for (let key in formErrors) {
            if (formErrors.hasOwnProperty(key) && formErrors[key].length > 0) {
                errorMessages.push(formErrors[key]);
            }
        }

        return errorMessages;
    }

    static validateForm(form: FormGroup,
        formErrors: object): boolean {
        return true;
    }
}



